@import "variables";

.btn {
    color: $white;
    background: $black;
    padding: 10px 20px;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
    transition: 0.5s;
    &:hover {
        background: $dark;
    }
}
.btn--sm {
    padding: 5px;
}
.btn--blank {
    color: $black;
    background: none;
    border: none;
    &:hover {
        background: none;
    }
}