@import "../../scss/variables";

.searchBarBoxShadow {
    box-shadow: 0 4px 8px -2px $muted;
}
.container {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    span {
        font-weight: 500;
    }
    input {
        outline: none;
        padding: 5px;
        margin: 0 5px;
        border-radius: 5px;
        border: none;
    }
    @media screen and (max-width: $break-small) {
        flex-direction: column;
        height: 100px;
        padding: 10px 0;
    }
}
.searchGroup {
    width: 50%;
    border: 1px solid $muted;
    border-radius: 5px;
    display: flex;
    align-items: center;
    padding: 0 5px;
    input {
        width: 100%;
    }
    @media screen and (max-width: $break-small) {
        width: 100%;
        margin-bottom: 5px;
    }
}
.imagesHeaderContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.dropdown {
    position: relative;
    display: inline-block;
    &:hover .dropdownContent {
        display: block;
    }
}
.dropdownContent {
    display: none;
    position: absolute;
    background: $white;
    right: 0;
    width: 200px;
    padding: 10px;
    border: 1px solid $muted;
    border-radius: 5px;
    z-index: 1;
}
.listAlign {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 5px 0;
    border-bottom: 1px solid $lightgrey;
}
.loaderContainer {
    display: flex;
    justify-content: center;
}
.loader {
    width: 100px;
    height: 100px;
}
.imagesPosition {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
    @media screen and (max-width: $break-medium) {
        grid-template-columns: 1fr 1fr;
    }
    @media screen and (max-width: $break-small) {
        grid-template-columns: 1fr;
    }
}
.images {
    width: 100%;
    height: 300px;
    object-fit: cover;
}
